*{
   margin: 0;
   padding: 0;
   list-style-type: none;
   box-sizing: border-box; 
}
img{
    cursor: pointer;
}
.App{
    background:url('./img/bg.jpg') fixed no-repeat center/cover;
    padding: 0 80px;
}
header{
    padding: 40px 0;
    text-align: center;
    color: white;
}
header h1{
    font-size: 50px;
}
header h1:hover, header p:hover{
    cursor: pointer;
}
.img1{
    width: 100%;
    margin: 0 auto;
    text-align: center;
}
.img1 img{
    width: 100%;
    box-shadow: 0 0 10px black;
}
